.common-button {
    border-radius: 5px;
    padding: 5px 4%;
    transition: 0.3s all ease-in-out;
    color: white;
    border: none;
    background: $teal;
    border: 2.5px solid $teal;
    font-family: 'Open Sans';
    font-weight: bolder;

    &:hover {
        background: $blue;
        border: 2.5px solid $blue;
    }

    @media (max-width: 1024px) {
        font-size: 1.563vw;
    }

    @media (max-width: 620px) {
        font-size: 2.581vw;
    }
}

.logged-out {
    background: white;
    border: 2.5px solid $primary;
    color: $primary;

    &:hover {
        color: white;
        border: 2.5px solid darken($green, 10%);
        background: darken($green, 10%);

    }
}